/* Cores */
.noticias-container {
  width: 600px;
  margin: 0 auto;
  display: flex;
}
.noticias-container .coluna-esquerda {
  width: 400px;
  padding: 25px 50px 25px 25px;
  background-color: #d9d9d9;
  border: 1px solid black;
}
.noticias-container .coluna-esquerda a {
  color: darkcyan;
  text-decoration: none;
}
.noticias-container .coluna-esquerda ul {
  padding: 0;
  margin: 0;
}
.noticias-container .coluna-esquerda ul li {
  list-style: none;
  padding-bottom: 15px;
}
.noticias-container .espacamento {
  height: 110px;
}
.noticias-container .coluna-direita {
  padding: 0 25px;
  background-color: #f5f5f5;
  border: 1px solid black;
}
.noticias-container .coluna-direita a {
  color: red;
  text-decoration: none;
}